<template>
  <div class="w-full h-screen bg-linear-to-b from-[#6dd5fa]  to-[#ffffff] overflow-auto">
    <userCard></userCard>
    <div class="text-[12px] bg-white rounded-sm p-1 m-1 flex justify-between">
      <div v-for="value in tabbar" class="flex flex-col items-center" :key="value.title"
        @click="router.push({ path: value.path })">
        <van-icon size="0.7rem" class="mb-1" :name="value.icon"></van-icon>
        <span>{{ value.title }}</span>
      </div>
    </div>
    <div class="bg-white p-1 m-1 rounded ">
      <div class="text-[12px] border-b p-1 border-gray-300 flex justify-between items-center">
        <h1 class="font-black text-[16px]">班级圈</h1>
        <van-checkbox v-model="select">仅看老师</van-checkbox>
        <div class="p-0.5 rounded-[10px] border border-green-500 inline-block">
          <van-icon name="search" size="0.5rem" class="align-middle" color="green"></van-icon>
          <span class="text-green-500 align-middle">搜索</span>
        </div>
      </div>
      <div class="box text-[12px] m-1">
        <div class="flex items-center w-full">
          <van-icon name="user" class="mr-1" size="1rem" color="green"></van-icon>
          <div class="flex flex-col">
            <span class="font-black mb-1">李军晓</span>
            <span class="text-gray-500">9月12日 10:00</span>
          </div>
          <div class="flex-1 flex justify-end">
            <van-icon name="ellipsis" size="0.5rem" />
          </div>
        </div>
        <div class="my-1">
          新型布置作业的方式
        </div>
        <div>
          <Image @click="containers('/public/image/黑板.jpg')" src="/public/image/黑板.jpg" class="rounded"></Image>
        </div>
        <div class="flex justify-end items-center">
          <!-- <div class="flex"> -->
          <van-icon @click="share" class="ml-1 " name="share-o" size="0.5rem"></van-icon>
          <van-icon class="ml-1" :name="goodJob" @click="goodJob = goodJob == 'good-job-o' ? 'good-job' : 'good-job-o'"
            size="0.5rem"></van-icon>
          <van-icon @click="comment" class="ml-1" name="comment-o" size="0.5rem"></van-icon>
          <van-popup position="bottom" v-model:show="show" :style="{ padding: '10px', backgroundColor: '#eeeeee' }"
            class="flex justify-center items-center">
            <!-- <van-cell-group inset> -->
            <van-field class="w-[70%]!" v-model="comments" label="评论" placeholder="请输入评论内容"></van-field>
            <van-button @click="() => {
              show = false
              commentss.push({
                comment: comments,
                time: new Date().toLocaleString(),
                user: '匿名用户'
              })
              comments = ''
            }">发表</van-button>
            <!-- </van-cell-group> -->

          </van-popup>
          <!-- </div> -->
        </div>
        <div>
          <van-cell v-for="(value, index) in commentss" :key="index" class="truncate flex-col">
            <template #title>
              <div class="w-full truncate">{{ `${value.user}: ${value.comment}` }}</div>
            </template>
          </van-cell>
        </div>
        <div class="text-center">
          <span>已经到底了</span>
        </div>
      </div>
    </div>
    <div @click="router.push({ path: '/publish' })" @touchmove="moving" ref="navRef"
      :style="{ left: left + 'px', top: top }"
      class="fixed z-4 flex-col flex justify-center items-center w-7 h-7 rounded-full! bg-green-400  shadow-lg shadow-cyan-500">
      <van-icon name="guide-o" size="0.7rem" color="white"></van-icon>
      <span class="text-[12px] text-white">发动态</span>
    </div>
    <div @click="globalImage = ''"
      class="fixed top-0 z-[-1] left-0 h-screen w-screen bg-fixed bg-center bg-no-repeat bg-contain" :style="{
        backgroundImage: globalImage ? `url(${globalImage})` : `url('')`,
        zIndex: globalImage ? 1 : -1,
        backgroundColor: globalImage ? 'black' : 'transparent'
      }">
    </div>
  </div>
</template>
<script setup lang='ts'>
import userCard from '@/components/userCard.vue';
import Image from '@/components/image.vue';
import { ref } from 'vue';
import { useRouter } from 'vue-router';
import { showSuccessToast, showFailToast } from 'vant'
import 'vant/es/toast/style'
const show = ref(false)
const globalImage = ref('')
const router = useRouter()
const select = ref<string>('')
const goodJob = ref('good-job-o')
const navRef = ref<HTMLElement>()
const left = ref<string | number>(0)
const top = ref<string | number>(0)
onMounted(() => {
  const width = document.documentElement.clientWidth as number
  left.value = `calc(${width}px - ${7 / 4}rem)`
  top.value = `calc(80% - ${7 / 4}rem)`
})
const time = ref(0)
const moving = (e: TouchEvent) => {
  if (time.value) {
    clearTimeout(time.value)
  }
  e.preventDefault()
  time.value = setTimeout(() => {
    const touch = e.touches[0] as Touch
    if (touch.clientX < 0 || touch.clientX > window.innerWidth - 100 || touch.clientY < 0 || touch.clientY > window.innerHeight - 100)
      return
    left.value = touch.clientX
    top.value = touch.clientY + 'px'
  }, 10);
}
const comment = () => {
  show.value = true
}
const containers = (url: string) => {
  globalImage.value = url
}
const comments = ref('')
const commentss = ref<Array<object>>([])
const share = () => {
  try {
    navigator.clipboard.writeText('这是最好的世界，也是最坏的世界').then(() => {
      showSuccessToast('复制成功')
    })
  }
  catch {
    showFailToast('复制失败')
  }

}
const tabbar = ref([{
  title: '班级相册',
  icon: 'card',
  path: '/classPhoto'
}, {
  title: '课程表',
  icon: 'printer',
  path: '/timeTable'
}, {
  title: '话题广场',
  icon: 'comment',
  path: '/topic'
}, {
  title: '食谱',
  icon: 'new',
  path: '/topicDetail'
}])
</script>
